// material
import { experimentalStyled as styled } from "@material-ui/core/styles";
// components
import Page from "../components/Page";
import {
  LandingHero,
  HomeScOne,
  HomeScTwo,
  HomeScThree,
  HomeScFour,
  HomeScFive,
  HomeScSix,
  HomeScSeven,
  HomeScEight,
} from "../components/_external-pages/landing";

// ----------------------------------------------------------------------

const RootStyle = styled(Page)(({ theme }) => ({
  height: "100%",
}));

const ContentStyle = styled("div")(({ theme }) => ({
  overflow: "hidden",
  position: "relative",
  backgroundColor: theme.palette.background.default,
}));

// ----------------------------------------------------------------------

export default function LandingPage() {
  return (
    <RootStyle title="Treedefi - The Sustainable yield farm" id="move_top">
      <LandingHero />
      <ContentStyle>
        <HomeScOne />
        <HomeScTwo />
        <HomeScThree />
        <HomeScFour />
        <HomeScFive />
        <HomeScSix />
        <HomeScSeven />
        <HomeScEight />
      </ContentStyle>
    </RootStyle>
  );
}
