import { Icon } from "@iconify/react";
import { motion } from "framer-motion";
import PaperIcon from "@material-ui/icons/Description";
import DonationIcon from "@material-ui/icons/VolunteerActivism";
// material
import { experimentalStyled as styled } from "@material-ui/core/styles";
import {
  Button,
  Box,
  Link,
  Container,
  Typography,
  Stack,
  StackProps,
} from "@material-ui/core";
// routes
import { PATH_DASHBOARD } from "../../../routes/paths";
//
import {
  varFadeIn,
  varFadeInUp,
  varWrapEnter,
  varFadeInRight,
} from "../../animate";

// ----------------------------------------------------------------------

const RootStyle = styled(motion.div)(({ theme }) => ({
  position: "relative",
  backgroundColor: theme.palette.grey[400],
  [theme.breakpoints.up("md")]: {
    top: 0,
    left: 0,
    width: "100%",
    height: "100vh",
    display: "flex",
    position: "relative",
    alignItems: "center",
  },
}));

const ContentStyle = styled((props: StackProps) => (
  <Stack spacing={5} {...props} />
))(({ theme }) => ({
  zIndex: 10,
  maxWidth: 520,
  margin: "auto",
  textAlign: "center",
  position: "relative",
  paddingTop: theme.spacing(15),
  paddingBottom: theme.spacing(15),
  [theme.breakpoints.up("md")]: {
    margin: "unset",
    textAlign: "left",
  },
}));

const HeroOverlayStyle = styled(motion.img)({
  zIndex: 9,
  width: "100%",
  height: "100%",
  objectFit: "cover",
  position: "absolute",
});

const HeroImgStyle = styled(motion.img)(({ theme }) => ({
  top: 0,
  right: 0,
  bottom: 0,
  zIndex: 9,
  width: "100%",
  margin: "auto",
  position: "absolute",
  [theme.breakpoints.up("md")]: {
    top: "-90px",
    right: "2%",
    width: "auto",
    height: "auto",
  },
  [theme.breakpoints.up("lg")]: {
    top: "-90px",
    right: "18%",
    width: "auto",
    height: "auto",
  },
}));

// ----------------------------------------------------------------------

export default function LandingHero() {
  return (
    <>
      <RootStyle initial="initial" animate="animate" variants={varWrapEnter}>
        <HeroOverlayStyle
          alt="overlay"
          src="/static/img/grdnt_bg.jpg"
          variants={varFadeIn}
        />

        <HeroImgStyle
          alt="hero"
          src="/static/home/home_b_img.png"
          variants={varFadeInUp}
          className="hero_img"
        />

        <Container maxWidth="lg" sx={{ pb: {xs: 5, lg: 5 }  }}>
          <ContentStyle>
            <motion.div variants={varFadeInRight}>
              <Typography
                variant="h1"
                sx={{ color: "common.white" }}
                className="bnnr_h1"
              >
                <Typography component="span" variant="h1">
                  The First
                </Typography>
                <br />
                Eco Friendly <br />
                DeFi Project.
              </Typography>
            </motion.div>

            <motion.div variants={varFadeInRight}>
              <Typography
                sx={{ color: "common.white" }}
                className="bnnr_p bnnr_p_wtmrgn"
              >
                Total Planted Trees (TPT)
              </Typography>
              <Box className="ttl_tr_bx">
                <Box component="img" src="/static/img/blck_tree.png" />
                <Box>
                  <Typography component="h3">{32810*21}</Typography>
                  <Typography component="p">
                    Kg of CO2 absorbed every year
                  </Typography>
                </Box>
              </Box>
            </motion.div>

            <motion.div variants={varFadeInRight}>
              <Button
                size="large"
                variant="contained"
                component={Link}
                href="https://whitepaper.treedefi.com"
                target="_blank"
                startIcon={<PaperIcon width={20} height={20} />}
                sx={{ marginRight: "25px" }}
                className="bnr_btn"
              >
                Whitepaper
              </Button>
              <Button
                size="large"
                variant="contained"
                component={Link}
                href="https://app.treedefi.com/donation"
                target="_blank"
                startIcon={<DonationIcon width={20} height={20} />}
                className="bnr_btn"
              >
                Donations
              </Button>
            </motion.div>
          </ContentStyle>
        </Container>
      </RootStyle>
      {/* <Box sx={{ height: { md: "100vh" } }} /> */}
    </>
  );
}
