import { alpha, useTheme, experimentalStyled as styled } from '@material-ui/core/styles';
import { Box, Grid, Card, Container, Typography, CardContent, CardActions, Button } from '@material-ui/core';
import { varFadeInUp, MotionInView, varFadeInDown, varFadeInLeft } from '../../animate';
import Slider from "react-slick";
import {Link} from 'react-router-dom'
import { BoxZoomHandler } from 'mapbox-gl';
import ArrowRightIcon from '@material-ui/icons/ArrowForward';


export default function HomeScEight() {
    var settings = {
        dots: true,
        infinite: false,
        speed: 500,
        slidesToShow: 3.9,
        centerMode: false,
        slidesToScroll: 1,
        responsive: [
            {
              breakpoint: 1279,
              settings: {
                slidesToShow: 3,
                slidesToScroll: 1,
              }
            },
            {
                breakpoint: 960,
                settings: {
                  slidesToShow: 2,
                  slidesToScroll: 1,
                }
              },
              {
                breakpoint: 576,
                settings: {
                  slidesToShow: 1,
                  slidesToScroll: 1,
                }
              },
        ]
      };
    return (
        <>
            <Box sx={{ mt: {xs: 5, lg: 10 }, mb: 8, }}>
                <Container maxWidth="lg">
                    <Grid container spacing={2}>
                        <Grid item xs={12} sx={{ mb: 3 }}>
                            <MotionInView variants={varFadeInDown}>
                                <Typography variant="h2" sx={{ textAlign: 'left' }}>
                                TreeDefi News
                                </Typography>
                                <Typography component="p" className="sb_ttl_txt sb_ttl_txt_lftalgn">
                                Our Medium Page is continuously updated with all of the new features, planting projects and anything else TreeDefi related.
                                </Typography>
                            </MotionInView>
                        </Grid>
                    </Grid>
                </Container>
                <Box className="slider_box">
                    <MotionInView variants={varFadeInUp}>
                        <Box className="drag_box">Drag</Box>
                        <Slider {...settings} className="def_item_slider">
                            <Box>
                                <Box className="sldr_bbx sldr_bbx03">
                                    <Typography component="label">New and massively improved</Typography>
                                    <Typography component="h3">📄 Whitepaper 2.0 </Typography>
                                    <Typography component="p">Our new whitepaper is finally out! </Typography>
                                    <Button size="small" className="arrowbtn" href="https://news.treedefi.com/litepaper-2-0-2b0e44a9397" target="_blank">Read More <ArrowRightIcon /></Button>
                                    <Typography component="h4">Jul 17, 2021</Typography>
                                </Box>
                            </Box>
                            <Box>
                                <Box className="sldr_bbx sldr_bbx04">
                                    <Typography component="label">Always keep your eyes open</Typography>
                                    <Typography component="h3">User Safety Announcement </Typography>
                                    <Typography component="p">We will never ask for your passwords, seed phrases, money or tokens. Stay safe please </Typography>
                                    <Button size="small" className="arrowbtn" href="https://news.treedefi.com/user-safety-announcement-ea2ac16468cf" target="_blank">Read More <ArrowRightIcon /></Button>
                                    <Typography component="h4">Jul 5, 2021</Typography>
                                </Box>
                            </Box>
                            <Box>
                                <Box className="sldr_bbx sldr_bbx01">
                                    <Typography component="label">1 Like = 1 Tree</Typography>
                                    <Typography component="h3">Tree Planting Social Media Campaign</Typography>
                                    <Typography component="p">1 Like =1 Tree Planted</Typography>
                                    <Button size="small" className="arrowbtn" href="https://news.treedefi.com/tree-planting-social-media-campaign-6e7f36f8e4c7" target="_blank">Read More <ArrowRightIcon /></Button>
                                    <Typography component="h4">Jun 18, 2021</Typography>
                                </Box>
                            </Box>
                            <Box>
                                <Box className="sldr_bbx sldr_bbx02">
                                    <Typography component="label">Merchandising</Typography>
                                    <Typography component="h3">TreeDefi Merchandising</Typography>
                                    <Typography component="p">You heard it right, it’s basically free!</Typography>
                                    <Button size="small" className="arrowbtn" href="https://news.treedefi.com/treedefi-merchandising-47d6a966b16e" target="_blank">Read More <ArrowRightIcon /></Button>
                                    <Typography component="h4">Jun 18, 2021</Typography>
                                </Box>
                            </Box>
                            <Box>
                                <Box className="sldr_bbx sldr_bbx03">
                                    <Typography component="label">Farewell Old Friend</Typography>
                                    <Typography component="h3">Slime Finance Partnership End</Typography>
                                    <Typography component="p">Farewell old friend, I hope to meet you again.</Typography>
                                    <Button size="small" className="arrowbtn" href="https://news.treedefi.com/slime-finance-partnership-end-4e90f63b52fa" target="_blank">Read More <ArrowRightIcon /></Button>
                                    <Typography component="h4">Jun 18, 2021</Typography>
                                </Box>
                            </Box>
                            <Box>
                                <Box className="sldr_bbx sldr_bbx04">
                                    <Typography component="label">Auto SEED</Typography>
                                    <Typography component="h3">Auto Seed Release!</Typography>
                                    <Typography component="p">As always, at TreeDefi we try to please our Treeinvestors!</Typography>
                                    <Button size="small" className="arrowbtn" href="https://news.treedefi.com/auto-seed-release-1fd9efe122df" target="_blank">Read More <ArrowRightIcon /></Button>
                                    <Typography component="h4">Jun 18, 2021</Typography>
                                </Box>
                            </Box>
                        </Slider>
                    </MotionInView>
                    
                </Box>
            </Box>
        </>
    );
}

