import {
  alpha,
  useTheme,
  experimentalStyled as styled,
} from "@material-ui/core/styles";
import {
  Box,
  Grid,
  Card,
  Container,
  Typography,
  useMediaQuery,
  Link,
} from "@material-ui/core";
import { varFadeInUp, MotionInView, varFadeInDown } from "../../animate";
import { maxWidth } from "@material-ui/system";

export default function HomeScOne() {
  return (
    <>
      <Box sx={{ pt: { xs: 10, lg: 10 }, pb: { xs: 10, lg: 5 } }}>
        <Container maxWidth="lg">
          <Grid container spacing={2} justifyContent="center">
            <Grid item xs={12}>
              <MotionInView variants={varFadeInUp}>
                <Typography component="p" className="sm_ttl" sx={{ mb: 2 }}>
                  Listed on:
                </Typography>
              </MotionInView>
            </Grid>
            <Grid item xs={12} md={10} className="text-center">
              <MotionInView variants={varFadeInUp}>
                <Link href="https://www.coinbase.com/price/treedefi" target="_blank">
                <Box className="tstd_bx">
                  <Box component="img" maxWidth="50%" src="/static/home/coinbase.png"></Box>
                </Box>
                </Link>
                <Link href="https://coinmarketcap.com/it/currencies/treedefi/markets/" target="_blank">
                <Box className="tstd_bx">
                  <Box component="img" maxWidth="50%" src="/static/home/coinmarketcap.png"></Box>
                </Box>
                </Link>
                <Link href="https://www.coingecko.com/en/coins/treedefi" target="_blank">
                <Box className="tstd_bx">
                  <Box component="img" maxWidth="50%" src="/static/home/coingecko.png"></Box>
                </Box>
                </Link>
                <Link href="https://www.dapp.com/app/treedefi" target="_blank">
                <Box className="tstd_bx">
                  <Box component="img" maxWidth="50%" src="/static/home/dapp.png"></Box>
                </Box>
                </Link>
                <Link href="https://coinmarketcap.com/it/currencies/treedefi/markets/" target="_blank">
                <Box className="tstd_bx">
                  <Box component="img" maxWidth="50%" src="/static/home/delta.png"></Box>
                </Box>
                </Link>
              </MotionInView>
            </Grid>
          </Grid>
        </Container>
      </Box>
    </>
  );
}
